import axios from 'axios'

function callRestServiceGetLstImg (errorsTab, callback) {
  axios
    .get('images')
    .then((response) => {
    // JSON responses are automatically parsed.
      callback(response.data)
    })
    .catch((e) => {
      errorsTab.push(e)
    })
}

function callRestServiceGetImg (id, callback, data) {
  const url = '/images/'+id
  axios.get(url, { responseType: 'blob' })
    .then((response) => {
      var reader = new window.FileReader()
      reader.readAsDataURL(response.data)
      reader.onload = function () {
        data.result = reader.result
        callback(data)
      }
    })
}

function callRestServiceDeleteImg (id) {
  const url = '/images/'+id
  axios.delete(url)
}

function callRestServicePostImg (data, callback) {
  axios.post('/images',
    data
  ).then(function () {
    callback()
  })
}

function callRestServiceGetInfoImg (id, callback) {
  const url = '/images/infos/'+id
  axios.get(url, {}
  ).then((response) => {
    callback(response.data)
  })
}

export { callRestServiceGetInfoImg, callRestServicePostImg, callRestServiceDeleteImg, callRestServiceGetLstImg, callRestServiceGetImg }
