<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <div>
        <div class="container">
            <label>Fichier
              <input type="file" id="file" ref="file" v-on:change="handleFileUpload()"/>
            </label>
              <button v-on:click="submitFile()">Soumettre</button>
        </div>
    </div>
    <Image id ="bigimg" v-if="showImg"  :callback="showGallery" :callback2="deleteImageSelected" ref ="childImg" />
    <Gallery id= "gal" v-else :callback="callImage" ref ="childGallery" />
  </div>
</template>
<script>
import Gallery from '@/components/Gallery.vue'
import Image from '@/components/Image.vue'
import { callRestServicePostImg, callRestServiceDeleteImg, callRestServiceGetImg } from '../http-api'

export default {
  name: 'Main',
  components: {
    Gallery,
    Image
  },
  props: {
    msg: String
  },
  data () {
    return {
      showImg: false,
      response: [],
      file: '',
      imageDataUrl: '',
      selected: 0,
      errors: []
    }
  },
  methods: {
    handleFileUpload () {
      this.file = this.$refs.file.files[0]
    },
    callbackSubmitFile () {
      if (this.$refs.childGallery != null){
        this.$refs.childGallery.refresh()
      }
    },
    submitFile () {
      const formData = new FormData()
      formData.append('file', this.file)
      callRestServicePostImg(formData, this.callbackSubmitFile)
    },
    deleteImageSelected () {
      this.showGallery()
      callRestServiceDeleteImg(this.selected)
    },
    showGallery () {
      this.showImg = false
    },
    callImage (image) {
      this.selected = image.id
      this.showImg = true
      const ref = this.$refs
      callRestServiceGetImg(image.id, ref.childImg.setImg, image )
    }
  }
}
</script>

